<style scoped>

</style>
<template>
  <div class="page">
    <div class="spin layout horizontal center">
      <Spin v-if="loading"></Spin>
      <Spin size="large" v-if="loading"></Spin>
      <Spin v-if="loading"></Spin>
    </div>
    <h2 class="page-title">
      班级宿舍
      <Button @click="refresh" type="text" size="small">
        <Icon type="refresh"></Icon>
        刷新
      </Button>
    </h2>
    <div class="section">
      <div class="page-toolbar layout horizontal center">
        <label for="">年级：</label>
        <Select placeholder="请选择年级" v-model="showGrade" style="width:200px">
          <Option v-for="item in gradeList" :value="item" :key="item">{{ item }}</Option>
        </Select>
        <Button @click="query">
          <Icon type="refresh"></Icon>
          查询
        </Button>
        <div class="flex"></div>
<!--         <Input placeholder="请输入..." style="width: 300px"></Input>
      <Button @click="query">
        <Icon type="search"></Icon>
        搜索
      </Button> -->
      <div class="flex"></div>
      <Button @click="exportData">
        <Icon type="android-download"></Icon>
        导出原始数据
      </Button>
      <Button @click="showNewModal = true">
        <Icon type="plus-round"></Icon>
        新增班级
      </Button>

    </div>
  </div>
  <div class="section">
    <Table border ref="table" no-data-text="空" :columns="columns2" :data="classesData"></Table>
  </div>
  <Back-top></Back-top>

  <div>
    <Modal v-model="showNewModal"
        title="新增班级宿舍">
      <div class="section">
        <Form :label-width="80">
          <Form-item label="年级：">
            <Input v-model="newClass.grade" style="width:200px">      
            <span slot="append">届</span>
          </Input>

        </Form-item>
        <Form-item label="班级：">
          <Input v-model="newClass.class" style="width:200px">      
          <span slot="append">班</span>
        </Input>

      </Form-item>
      <Form-item label="班主任：">
        <Input v-model="newClass.teacher" style="width:200px"></Input>

      </Form-item>
      <Form-item label="宿舍：">
      <ul>
        <li v-for="(r,ind) in newClass.rooms">
          <div class="layout horizontal center">
            <Button type="text" size="small" @click="delRoom(ind)">
              <Icon type="close-round"></Icon>
            </Button>
            <Select placeholder="院子" size="small" v-model="r.garden" style="width:100px">
              <Option v-for="(item,index) in gardenList" :value="item" :key="index">{{ item }}</Option>
            </Select>
            <Select placeholder="性别" size="small" v-model="r.sex" style="width:100px">
              <Option v-for="(item,index) in sexList" :value="item" :key="index">{{ item }}</Option>
            </Select>
            <Input style="width:100px" size="small" v-model="r.roomnumber" placeholder="房间"></Input>
          <Input style="width:150px" size="small" v-model="r.leader" placeholder="舍长"></Input>
      </div>
      </li>
      <li>
      <Button size="small" type="dashed" style="width:100px;" @click="addRoom">
        <Icon type="plus-round"></Icon>
      </Button>

      </li>

      </ul>

      </Form-item>
      </Form>

      </div>
  <div slot="footer">
    <div class="action layout horizontal">
      <div class="flex"></div>
      <Button @click="reset">重置</Button>
      <Button type="primary" @click="createClass">完成</Button>
    </div>
  </div>
  </Modal>

  <Modal v-model="showEditModal"
        title="编辑班级宿舍">

    <Form :label-width="80">
      <Form-item label="年级：">
        <Input v-model="newClass.grade" style="width:200px">        
        <span slot="append">届</span>
        </Input>

        </Form-item>
        <Form-item label="班级：">
        <Input v-model="newClass.class" style="width:200px">        
        <span slot="append">班</span>
        </Input>
        </Form-item>
        <Form-item label="班主任：">
        <Input style="width:200px" v-model="newClass.teacher"></Input>

        </Form-item>
        <Form-item label="宿舍：">
        <ul>
        <li v-for="(r,ind) in newClass.rooms">
        <div class="layout horizontal center">
          <Button size="small" @click="delRoom(ind)">
            <Icon type="close-round"></Icon>

          </Button>
          <Select placeholder="院子" size="small" 
                v-model="r.garden" style="width:100px">
            <Option v-for="(item,index) in gardenList" :value="item" :key="index">{{ item }}</Option>
          </Select>
          <Select placeholder="性别" size="small" v-model="r.sex" style="width:100px">
            <Option v-for="(item,index) in sexList" :value="item" :key="index">{{ item }}</Option>
          </Select>
          <Input style="width:100px" size="small" v-model="r.roomnumber" placeholder="房间"></Input>
        <Input style="width:150px" size="small" v-model="r.leader" placeholder="舍长"></Input>
        </div>
        </li>
        <li>
        <Button size="small" type="dashed" style="width:100px;" @click="addRoom">
        <Icon type="plus-round"></Icon>
        </Button>
        </li>

        </ul>

        </Form-item>

        </Form>
        <div class="action layout horizontal">
        <div class="flex"></div>

        </div>

        <div slot="footer">
        <div class="action layout horizontal">
        <div class="flex"></div>
        <Button @click="cancel">取消</Button>
        <Button type="primary" @click="saveClass">保存</Button>
        </div>
        </div>
        </Modal>
  </div>

</div>

</template>
<script>
'use strict'

import config from '../config'
import utils from '../utils'
import axios from 'axios'

export default {
  data () {
    return {
      loading: false,
      showConfirm: true,
      showGrade: '2018',
      showNewModal: false,
      showEditModal: false,
      newRoom: {
        garden: '',
        sex: '',
        roomnumber: '',
        leader: ''
      },
      newClass: {
        grade: '',
        class: '',
        teacher: '',
        rooms: []
      },
      gardenList: [
        '和苑', '雅苑', '馨苑'
      ],
      sexList: [
        '男生', '女生'
      ],
      gradeList: [],
      columns2: [{
        type: 'index',
        width: 80,
        title: '序号',
        className: 'textbf'
      }, {
        title: '年级',
        key: 'grade',
        className: 'textbf',
        width: 100
      }, {
        title: '班级',
        key: 'class',
        className: 'textbf',
        width: 100
      }, {
        title: '班主任',
        key: 'teacher',
        className: 'textbf'
      }, {
        title: '房间',
        key: 'rooms',
        render: (h, params) => {
          return h('ul', this.roomColRender(h, params.row))
        }
      },
      {
        title: '操作',
        key: 'action',
        width: 160,
        render: (h, params) => {
          return h('div', [
            h('Button', {
              props: {
                type: 'text',
                size: 'small'
              },
              style: {
                fontSize: '14px'
              },
              on: {
                click: () => {
                  this.editClass(params.row)
                }
              }
            }, '编辑'),
            this.delConfirmRender(h, params.row)
          ])
        }
      }],
      classesData: []
    }
  },
  created: function () {
    this.fetchGrade()
  },
  methods: {
    refresh () {
      this.fetchGrade()
    },
    query () {
      this.loading = true
      this.fetchData()
      setTimeout(() => {
        this.loading = false
      }, 1000)
    },
    reset () {
      this.newClass = {
        // grade: '',
        // class: '',
        // teacher: '',
        rooms: []
      }
    },
    prepareData () {
      let e = this.cloneObj(this.classesData)
      e.forEach((a) => {
        a = this.assembleRow(a)
      })
      return e
    },
    cloneObj (obj) {
      let newObj = {}
      if (obj instanceof Array) {
        newObj = []
      }
      for (let key in obj) {
        let val = obj[key]
        newObj[key] = typeof val === 'object' ? this.cloneObj(val) : val
      }
      return newObj
    },
    assembleRow (row) {
      let rs = ''
      row.rooms.forEach((a) => {
        let s = ''
        for (let i in a) {
          s += a[i] + '-'
        }
        s = s.substring(0, s.length - 1)
        rs += s + ';'
      })
      row.rooms = rs
      return row
    },
    exportData () {
      this.$refs.table.exportCsv({
        filename: '班级数据',
        columns: this.columns2.filter((col, index) => index <
          5),
        data: this.prepareData()
      })
    },
    delConfirmRender (h, row) {
      let btn = h('Button', {
        props: {
          type: 'text',
          size: 'small'
        },
        style: {
          fontSize: '14px'
        }
      }, '删除')
      let okBtn = h('Button', {
        props: {
          type: 'warning',
          size: 'small'
        },
        style: {
          marginTop: '10px'
        },
        on: {
          click: () => {
            this.delClass(row)
          }
        }
      }, '删除')
      let btnLine = h('div', {
        class: 'layout horizontal'
      }, [h('div', {class: 'flex'}, ''), okBtn])
      let titleStr = ' ' + row.grade + '届' + row.class + '班 '
      let title = h('div', {
        slot: 'title'
      }, '确认删除？')
      let tipStr = h('div', {
        style: {
          fontSize: '14px'
        }
      }, titleStr + '的数据将会被删除。')
      let warningStr = h('div', {
        style: {
          color: '#ff0000',
          fontSize: '14px'
        }
      }, '警告：此操作不可恢复！')
      let content = h('div', {
        slot: 'content'
      }, [tipStr, warningStr, btnLine])

      let pop = h('Poptip', {
        props: {
        }
      }, [btn, title, content])
      return pop
    },
    roomColRender: function (h, row) {
      let rooms = []
      let str = ''

      for (let n in row.rooms) {
        str += row.rooms[n].garden + '-' +
          row.rooms[n].sex + '-' +
          row.rooms[n].roomnumber + '-' + row.rooms[n].leader
        let cc = h('li', str)
        rooms.push(cc)
        str = ''
      }
      return rooms
    },
    delRoom: function (ind) {
      this.newClass.rooms.splice(ind, 1)
    },
    addRoom: function () {
      this.newClass.rooms.push(this.newRoom)
      this.newRoom = {
        garden: '',
        sex: '',
        roomnumber: '',
        leader: ''
      }
    },
    cancel: function () {
      this.showEditModal = false
      this.reset()
    },
    fetchGrade () {
      const that = this
      const prefix = config.service.apiUrl
      const url = prefix + '/utils/grades'

      axios.get(url)
        .then(function (res) {
          that.gradeList = res.data[0].grades.slice().sort(utils.sortNumber)
          that.showGrade = that.gradeList[0]
          that.fetchData()
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    fetchData: function () {
      const that = this
      const prefix = config.service.apiUrl
      const url = prefix + '/classes?grade=' + this.showGrade

      axios.get(url)
        .then(function (res) {
          let classes = res.data.slice()
          that.classesData = classes.sort(utils.sortClasses)
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    delClass: function (row) {
      const prefix = config.service.apiUrl
      const url = prefix + '/classes/' + row._id
      const that = this
      axios.delete(url)
        .then(function (res) {
          that.fetchData()
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    editClass: function (row) {
      this.showEditModal = true
      this.newClass = row
    },
    saveClass: function () {
      const prefix = config.service.apiUrl
      const url = prefix + '/classes/' + this.newClass._id
      const that = this
      axios.put(url, this.newClass)
        .then(function (res) {
          that.showEditModal = false
          that.fetchData()
          that.reset()
          that.$Message.info('更新了一个班级的数据。')
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    createClass: function () {
      const prefix = config.service.apiUrl
      const url = prefix + '/classes'
      const that = this

      axios.post(url, this.newClass)
        .then(function (res) {
          that.showNewModal = false
          that.fetchData()
          that.reset()
          that.$Message.info('新增一个班级的数据。')
        })
        .catch(function (err) {
          console.log(err)
        })
    }
  }
}
</script>



